@-webkit-keyframes u-clock-fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes u-clock-fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
* {
  -webkit-transform-origin: inherit;
          transform-origin: inherit;
}
.u-clock {
  -webkit-transform-origin: 50px 50px;
          transform-origin: 50px 50px;
  -webkit-animation-name: u-clock-fade-in;
          animation-name: u-clock-fade-in;
  -webkit-animation-duration: 500ms;
          animation-duration: 500ms;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  background-color: #fff;
  border-radius: 50%;
}
.u-clock-hand {
  transition: -webkit-transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275), -webkit-transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
